@import './_typography';
@import './_definitions';

main {
  font: $homeCityFont;
  text-transform: uppercase;
  width: 100vw;
  height: 100vh;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  object-fit: cover;
  display: flex;
  justify-content: center;
  align-content: center;
  transition: $boxTransition;

  h1 {
    margin: 0 auto;
    position: absolute;
    top: 40vh;
    color: white;
    text-align: center;
  }
}